import React, { Component } from 'react';
import { Bar } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend,
} from 'chart.js';

ChartJS.register(
    CategoryScale,
    LinearScale,
    BarElement,
    Title,
    Tooltip,
    Legend
);
export const options = {
responsive: true,
plugins: {
    legend: {
    position: 'top',
    },
    title: {
    display: true,
    text: 'Chart.js Bar Chart',
    },
},
};

const labels = ['10', '20', '30', '40', '50', 'June', 'July'];

export const data = {
    labels,
    ddatasets:
    { //
        label: 'test1', // 
        fill: false, // line  ,    
        data: [
            21, 19, 25, 20, 23, 26, 25 //x label   
        ],
        backgroundColor: [
            //
            'rgba(255, 99, 132, 0.2)',
            'rgba(54, 162, 235, 0.2)',
            'rgba(255, 206, 86, 0.2)',
            'rgba(75, 192, 192, 0.2)',
            'rgba(153, 102, 255, 0.2)',
            'rgba(255, 159, 64, 0.2)'
        ],
        borderColor: [
            // 
            'rgba(255, 99, 132, 1)',
            'rgba(54, 162, 235, 1)',
            'rgba(255, 206, 86, 1)',
            'rgba(75, 192, 192, 1)',
            'rgba(153, 102, 255, 1)',
            'rgba(255, 159, 64, 1)'
        ],
        borderWidth: 1 // 
    }
}

class RightGraph extends Component {
    render() {
        return (
            <div>
                <Bar data={data} options={options}
                style={{ width: "20vw", height: "20vh", margin:"30px auto" }}/>
            </div>
        );
    }
}

export default RightGraph;